@import url('https://fonts.googleapis.com/css?family=Lora|Montserrat:400,700,800');

.Project-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
}

.Project-photo {
    position: absolute;
    width: 350px;
    height: 400px;
    margin: 10px;
    border-radius: 38px;
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.Overlay {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    z-index: 1;
    width: 350px;
    height: 400px;
    opacity: 0;
    margin: 10px;
    background-color: white;
    text-align: center;
    -webkit-transition: .6s ease;
    -o-transition: .6s ease;
    transition: .6s ease;
    border-radius: 38px;
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.Overlay:hover:not(.Project-title) {
    opacity: 0.95;
}

.Project-title {
    position: block;
    margin-top: 90px;
    opacity: 1;
    color: #A1CEC7;
    z-index: 100;
    font-family: 'Montserrat', sans-serif;
    font-size: 21px;
}

.Project-title:hover {
    color: #F49E85;
    opacity: 1;
}

.Project-descrip {
    margin: 20px auto;
    width: 75%;
    opacity: 1;
    color: gray;
    z-index: 100;
    font-family: 'Lora', serif;
    font-size: 15px;
}


